import React,  {useState, useEffect} from 'react';
import './App.css'
import Header from './Components/Header'
import Sidebar from './Components/Sidebar'
import Contents from './Components/Contents'
import jQuery from 'jquery';
let $ = window.jQuery = jQuery;
function App(){
	const [url,setUrl] = useState('');
	const [sidebaronoff, setsidebaronoff] = useState(1);
	const [title,setTitle] = useState('HOME');
	var sidebarlist=[];
	const [blogcontent,setblogcontent] = useState(sidebarlist);
	$(function(){
		if($(window).width() < 1600) {
			$(".toggle").show();
			$(".Sidebar").css("left",sidebaronoff*-300);
			(!sidebaronoff) ? $(".Shadow").css("box-shadow", "0 0 0 9999px rgba(0,0,0,0.5)") : $(".Shadow").css("box-shadow", "0 0 0 9999px rgba(0,0,0,0)")
		}
		else {
			setsidebaronoff(0);
			$(".toggle").hide();
			$(".Sidebar").css("left",0);
			$(".Shadow").css("box-shadow", "0 0 0 9999px rgba(0,0,0,0)");

		}
	});
	
	return (

		<div>
			<Header k={setUrl} sidebaronoff={sidebaronoff} setsidebaronoff={setsidebaronoff} title={title}/>
			<Sidebar sidebaronoff={sidebaronoff} setsidebaronoff={setsidebaronoff} url={url} blogcontent={blogcontent}/>
			<Contents url={url} setUrl={setUrl} setTitle={setTitle} setblogcontent={setblogcontent}/>
		</div>
	)
}
export default App;